<template>
  <div v-if="shutDown">
    <ShutDown @start="start" />
  </div>
  <div v-else-if="bootup">
    <Bootup />
  </div>
  <div v-else>
    <Desktop />
    <Taskbar />
  </div>
</template>

<script>
import Desktop from './components/Desktop.vue';
import Bootup from './components/Bootup.vue';
import ShutDown from './components/ShutDown.vue';
import Taskbar from './components/Taskbar.vue';

export default {
  name: 'App',
  components: {
    Bootup,
    Desktop,
    Taskbar,
    ShutDown,
  },
  data() {
    return {
      shutDown: true,
      bootup: false,
    };
  },
  methods: {
    start() {
      this.shutDown = false;
      this.bootup = true;
      setTimeout(() => {
        this.bootup = false;
      }, 2000);
    },
  },
}
</script>

<style>
#app {
  margin: 0px;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
